import React from 'react';
import styled from 'styled-components';


const ResultSection = ({ result }) => {
  
  console.log(result)

  return (
    <ResulContainer>
      <InfoContainer>
        <p className="infoTitle">{result.name} {result.position}</p>
        <p className="resultInfo">
          {result.session} <br/>
          {result.index <= 70 ? '본당으로 ' : result.index > 80 ? "" : '베다니홀로 '} 
          {result.reservation === true ? '신청되셨습니다.' : '신청 되지 못 했습니다.'}
        </p>
      </InfoContainer>
    </ResulContainer>
  );
}

const ResulContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 768px) {
    max-width: 768px;
  }
`;

const InfoContainer = styled.div`
  width: 100%;
  padding: 2rem;
  color: #fff;
  font-size: 1.6rem;
  font-weight: lighter;
  background: rgb(101, 177, 177);
  line-height: 1.6;

  .infoTitle {
    font-size: 2.4rem;
    font-weight: bold;
    letter-spacing: 0.2rem;
    padding-bottom: 3rem;
  }
  .resultInfo {
    font-size: 2.4rem;
    padding-bottom: 3rem;
  }
`;

