import React from 'react';
import styled from 'styled-components';

import barcode from '../assets/barcode.jpg'
import { colors } from '../styles/colors';

const Ticket = ({ checkResult }) => {

  const getTime = (time) => {
    switch (true) {
        case time === '1부예배':
          return '오전 7:10'
        case time === '2부예배':
          return '오전 8:00'
        case time === '3부예배':
          return '오전 9:30'
        case time === '4부예배':
          return '오전 11:00'
        case time === '6부예배':
          return '오후 4:00'
        default:
          return '오전 7:10'
      }
    }

  return (
    <Container>
      <Header>
        <div style={{ fontSize: '4rem', fontWeight: 800, lineHeight: 1.2}}>티켓</div>
        <div style={{ fontSize: '4rem', fontWeight: 800, lineHeight: 0.8, textAlign: 'end'}}>TICKET</div>
      </Header>
      <Body>
        <div style={{fontSize: '2rem', fontWeight: 600, marginBottom: '1.75rem'}}>{checkResult.name} {checkResult.position}님 </div>
        <ResultContainer>
          {checkResult.reservation === false ? (
            <div style={{display: 'flex', flexDirection: 'column'}}>
              <span style={{fontSize: '2.25rem', fontWeight: 600, color:'red', marginBottom:'0.75rem', textDecoration: 'underline black'}}>신청이 마감되었습니다</span>
              <span style={{fontSize: '2.25rem', fontWeight: 600 }}>죄송합니다</span>
            </div>
          ) : (
            <span style={{borderBottom: '1px solid black', fontSize: '1.5rem'}}>
              <span style={{fontSize: '2.5rem', fontWeight: 600, color:'red'}}>
                {checkResult.index <= 70 ? '본당' : checkResult.index > 80 ? "" : '베다니홀 '}
              </span>
              에 신청되셨습니다
            </span>
          )}
        </ResultContainer>
        <DetailContainer>
          <Detail>
            <div style={{fontSize:'1.75rem'}}>주일</div>
          </Detail>
          <Detail>
            <div style={{fontSize:'1.75rem'}}>{checkResult.session.substring(0,2)}</div>
            <div style={{fontSize:'1rem', color:'#A4A4A4'}}>예배</div>
          </Detail>
        </DetailContainer>
        <DetailContainer>
          <Detail>
            <div style={{fontSize:'1.75rem'}}>{getTime(checkResult.session)}</div>
            <div style={{fontSize:'1rem', color:'#A4A4A4'}}>시간</div>
          </Detail>
        </DetailContainer>
        <Barcode src={barcode} />
      </Body>
    </Container>
  );
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 85%;
`;

const Header = styled.div`
  display: flex;
  flex-direction: column;
  background-color: ${colors.LIGHTBLUE400};
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
`;

const Body = styled.div`
  padding: 1.5rem 1rem;
  background-color: #e2e2e2;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
`;

const ResultContainer = styled.div`
  margin-bottom: 4rem;
`;

const DetailContainer = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 2rem;
`;

const Detail = styled.div`
  flex: 1;
`;
const Barcode = styled.img`
  margin-top: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default Ticket;