import React, { useState, useEffect, useContext } from 'react';
import styled from 'styled-components';
import { colors } from '../styles/colors';
import { MdLocationOn } from 'react-icons/md'
import LinkedButton from './LinkedButton';
import { db } from '../config/firebaseConfig';

import { SettingContext } from '../context/SettingContext';

const Card = ({ session, sessionName, time, notice, seats, closing, thumbnail, sunday, bgColor }) => {
  const [ isOpening, setIsOpening ] = useState(closing)
  const [ remainingSeats, setRemainingSeats ] = useState(seats)
  const [ setting ] = useContext(SettingContext)


  useEffect(() => {
    db.collection('주일예배').doc(sessionName).collection(setting.sunday).doc('--seats--').onSnapshot((doc) => {
      const tempCount = doc.data().RemainingSeats
      setRemainingSeats(tempCount)
    }, (error) => {
      console.log("Error @ListenDB: ", error)
    })

  }, [])

  return (
      <Container>
        <ThumbnailContainer bgColor={bgColor}>
          {/* <img src={thumbnail} alt="" /> */}
          <p className="session">{sessionName}</p>
          <p className="time">{time}</p>
        </ThumbnailContainer>
        <InfoContainer>
          {notice ? (
            <TextContainer>
              [공지] {notice}
            </TextContainer>
          ): null}
          <PlacesContainer>
            <Row>
              <Left>
                <MdLocationOn/>
                본당
              </Left>
              <Right>{remainingSeats > 10 ? remainingSeats-10 : 0} 석</Right>
            </Row>
            <Row>
              <Left>
                <MdLocationOn/>
                베다니홀
              </Left>
              <Right>{remainingSeats > 10 ? 10 : remainingSeats <= 0 ? 0 : remainingSeats} 석</Right>
            </Row>
          </PlacesContainer>
          <BtnContainer>
            <LinkedButton 
              openState={isOpening} 
              remainingSeats={remainingSeats} 
              to={`/service-reservation/booking?session=${session}`}
            />
          </BtnContainer>
        </InfoContainer>
      </Container>
  );
}

const Container = styled.div`
  height: 18rem;
  display: flex;
  margin-bottom: 2rem;
`;

const ThumbnailContainer = styled.div`
  min-width: 12rem;
  height: 100%;
  border: 1px solid ${props => props.bgColor};
  background-color: ${props => props.bgColor};
  border-radius: 10px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.2);
  color: ${colors.WHITE};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;

  ::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 1px solid rgba(0,0,0,0.06);
    background-color: rgba(0,0,0,0.06);
    border-radius: 10px;
    z-index: 1;
    pointer-events: none;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
  }

  .session {
    font-size: 1.6rem;
    font-weight: bold;
    padding-bottom: 1rem;
    letter-spacing: 0.1rem;
    z-index: 2;
  }
  .time {
    font-size: 1.8rem;
    font-weight: bold;
    letter-spacing: -0.1rem;
    z-index: 2;
  }
`;

const InfoContainer = styled.div`
  min-width: 20rem;
  width: 35rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0.6rem 0;
`;

const TextContainer = styled.div`
  width: 100%;
  display: flex;
  font-size: 1.4rem;
`;

const PlacesContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 1.6rem;
  flex: 1;
`;

const Row = styled.div`
  display: flex;
  margin-top: 2rem;
`;
const Left = styled.div`
  flex: 1;
`;
const Right = styled.div`
  display: flex;
  justify-content: center;
`;

const BtnContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
`;

export default Card;