import React, { useState } from 'react';
import styled from 'styled-components';
import DatePicker from "react-datepicker";

import InnerContainer from '../../../components/Container/InnerContainer';
import { db } from '../../../config/firebaseConfig';

function fillZero(width, str){
  return str.length >= width ? str:new Array(width-str.length+1).join('0')+str;//남는 길이만큼 0으로 채움
}


const Setting = () => {
  const [ title, setTitle ] = useState({
    title: "8월 15일 주일예배",
    subtitle: "8월 12일(목) 저녁 8:00"
  })
  const [ seats, setSeats ] = useState({
    sessionOne : 80,
    sessionTwo : 80,
    sessionThree : 80,
    sessionFour : 80,
    sessionFive: 0,
    sessionSix : 80,
  })
  const [ notices, setNotices ] = useState({
    sessionOne: "영상예배입니다",
    sessionTwo: "",
    sessionThree: "",
    sessionFour: "",
    sessionFive: "",
    sessionSix: "",
  })
  const [ closing, setClosing ] = useState({
    sessionOne: false,
    sessionTwo: false,
    sessionThree: false,
    sessionFour: false,
    sessionFive: true,
    sessionSix: false,
  })

  let sunday = new Date();
  sunday.setDate(sunday.getDate() + (0 + 7 - sunday.getDay()) % 7);
  const sundayString = `${sunday.getFullYear()}${fillZero(2, (sunday.getMonth()+1).toString())}${sunday.getDate()}`
  
  const batch = db.batch();

  const onClick = () => {
    const firstSeatRef = db.collection('주일예배').doc('1부예배').collection(sundayString).doc('--seats--')
    const secondSeatRef = db.collection('주일예배').doc('2부예배').collection(sundayString).doc('--seats--')
    const thirdSeatRef = db.collection('주일예배').doc('3부예배').collection(sundayString).doc('--seats--')
    const forthSeatRef = db.collection('주일예배').doc('4부예배').collection(sundayString).doc('--seats--')
    const fifthSeatRef = db.collection('주일예배').doc('5부예배').collection(sundayString).doc('--seats--')
    const sixthSeatRef = db.collection('주일예배').doc('6부예배').collection(sundayString).doc('--seats--')
    const settingRef = db.collection('setting').doc('latest')
    batch.set(firstSeatRef, { RemainingSeats: seats.sessionOne })
    batch.set(secondSeatRef, { RemainingSeats: seats.sessionTwo })
    batch.set(thirdSeatRef, { RemainingSeats: seats.sessionThree })
    batch.set(forthSeatRef, { RemainingSeats: seats.sessionFour })
    batch.set(fifthSeatRef, { RemainingSeats: seats.sessionFive })
    batch.set(sixthSeatRef, { RemainingSeats: seats.sessionSix })
    batch.set(settingRef, {
      sunday: sundayString,
      openingDate: [sunday.getFullYear(), sunday.getMonth(), sunday.getDate()-3, 20, 0],
      title: title.title,
      subtitle: title.subtitle,
      sessionOneSeats: seats.sessionOne,
      sessionTwoSeats: seats.sessionTwo,
      sessionThreeSeats: seats.sessionThree,
      sessionFourSeats: seats.sessionFour,
      sessionFiveSeats: seats.sessionFive,
      sessionSixSeats: seats.sessionSix,
      sessionOneNotice: notices.sessionOne,
      sessionTwoNotice: notices.sessionTwo,
      sessionThreeNotice: notices.sessionThree,
      sessionFourNotice: notices.sessionFour,
      sessionFiveNotice: notices.sessionFive,
      sessionSixNotice: notices.sessionSix,
      sessionOneClosing: closing.sessionOne,
      sessionTwoClosing: closing.sessionTwo,
      sessionThreeClosing: closing.sessionThree,
      sessionFourClosing: closing.sessionFour,
      sessionFiveClosing: closing.sessionFive,
      sessionSixClosing: closing.sessionSix,
    })
    batch.commit()
    .then(() => {
      console.log("Document successfully written!");
      alert('업데이트 성공했습니다')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('업데이트 실패했습니다')
    });
  }

  return (
    <InnerContainer>
      <SettingContainer>
        <Button onClick={onClick}>
          업데이트
        </Button>
      </SettingContainer>
    </InnerContainer>
  );
}

const SettingContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lavender;
`;

const Button = styled.button`
  margin: 2rem 0;
  padding: 1rem 2rem;
`;





export default Setting;