import React, { useState, useContext } from 'react';
import styled from 'styled-components';

import InnerContainer from '../../../components/Container/InnerContainer';
import CountDown from '../../../components/CountDown';
import bgImage from '../../../assets/bannerview2.jpeg';
import { SettingContext } from '../../../context/SettingContext';


const HeroSection = () => {
  const [ oepning, setOepning ] = useState(false)
  const [ setting ] = useContext(SettingContext)

  const { openingDate, title, subtitle } = setting


  return (
    <InnerContainer>
      <HeroContainer>
        <HeroBackgroundContainer>
          <img src={bgImage} alt="" />
        </HeroBackgroundContainer>
        <HeroContentContainer>
          <MessageBoxContainer>
            <div className="label blinking">
              {oepning ? "지금 신청 중" : "신청 대기 중"}
            </div>
            <p className="title">{title}</p>
            <p className="date">신청시간 | {subtitle}</p>
          </MessageBoxContainer>
          <CountDownContainer>
            <CountDown 
              date={new Date(openingDate[0], openingDate[1], openingDate[2], openingDate[3], openingDate[4])} 
              Oepning={setOepning}/>
          </CountDownContainer>
        </HeroContentContainer>
      </HeroContainer>
    </InnerContainer>
  );
}

const HeroContainer = styled.div`
  width: 100%;
  height: 50vh;
  position: relative;

  @media screen and (min-width: 768px) {
    height: 320px;
  }
`;

const HeroBackgroundContainer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;

  ::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 1px solid rgba(0,0,0,0.06);
    background-color: rgba(0,0,0,0.06);
    z-index: 1;
    pointer-events: none;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @media screen and (min-width: 768px) {
    img {
      width: 100%;
      height: 100%;
      object-fit: fill;
    }
  }
`;


const HeroContentContainer= styled.div`
  position: absolute;
  z-index: 3;
  width: 100%;
  height: 100%;
  display: grid;
  grid-auto-rows: 1.75fr 1.25fr;
  grid-auto-columns: minmax(auto, 1fr);
  grid-template-areas: 
    "message"
    "countdown"
  ;

  @media screen and (min-width: 768px) {
    display: grid;
    grid-template-columns: 300px 400px;
    grid-gap: 3rem;
    align-content: center;
    justify-content: center;
    grid-template-areas: 
      "message countdown" 
    ;
  }
`;

const MessageBoxContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .label {
    min-width: 230px;
    border-radius: 12px;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.7rem 0.7rem;
    margin-bottom: 1.8rem;
    font-size: 1.6rem;
    background : rgba(0,0,0,0.2);
  }
  .title {
    color: #fff;
    letter-spacing: -0.3px;
    font-size: 3rem;
  }
  .date {
    margin-top: 1.5rem;
    color: #fff;
    font-size: 1.8rem;
  }

  .blinking{ 
    -webkit-animation:blink 1.3s ease-in-out infinite alternate; 
    -moz-animation:blink 1.3s ease-in-out infinite alternate; 
    animation:blink 1.3s ease-in-out infinite alternate; } 
    @-webkit-keyframes blink { 
      0% { 
        opacity: 0; 
      } 
      100% {
        opacity: 1;
      }
    } 
    @-moz-keyframes blink { 
      0% {
        opacity: 0;
      } 
      100% {
        opacity: 1;
      } 
    } 
    @keyframes blink { 
      0% {
        opacity: 0;
      } 
      100% { opacity: 1;
      } 
    }


  @media screen and (min-width: 768px) {

    .label {
      min-width: 230px;
      padding: 1rem 1.25rem;
      margin-bottom: 1.5rem;
    }
  }

`;

const CountDownContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;


export default HeroSection;