import React from 'react';
import styled from 'styled-components';
import { Controller, useForm } from "react-hook-form";


import InnerContainer from '../../../components/Container/InnerContainer';
import TextInput from '../../../components/TextInput';
import Button from '../../../components/Button';
import { auth } from '../../../config/firebaseConfig';


const Login = ({ setIsLoggedIn }) => {
  const { register, handleSubmit } = useForm();

  const onSubmit = async (data) => {
    auth.signInWithEmailAndPassword(data.email, data.password)
    .then(() => {
      setIsLoggedIn(true)
    })
    .catch((error) => {
      console.log(error.message)
    })
  }

  return (
    <InnerContainer>
      <LoginContainer>
        <FormTitle>관리자 로그인</FormTitle>
        <FromContainer onSubmit={handleSubmit(onSubmit)}>
          <TextInput name="email" label="이메일" register={register}/>
          <TextInput name="password" type="password" label="비밀번호" register={register}/>
          <ButtonContainer>
            <Button type="submit">로그인</Button>
          </ButtonContainer>
        </FromContainer>
      </LoginContainer>
    </InnerContainer>
  );
}

const LoginContainer = styled.div`
  width: 100%;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const FormTitle = styled.p`
  color: #1e1e23;
  letter-spacing: -0.3px;
  font-size: 2.2rem;
  margin: 3rem 0;
`;

const FromContainer = styled.form`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 10%;

  @media screen and (min-width: 768px) {
    width: 60%;
  }
`;

const ButtonContainer = styled.div`
  width: 100%;
  margin-top: 4rem;
`;


export default Login;