import React from 'react';
import styled from 'styled-components';
import { colors } from '../styles/colors';

const TextInput = React.forwardRef(({ label, name, type, register, ...props}, ref) => {

  return (
    <Form>
      <Input 
        type={type}
        autoCapitalize="false"
        autoCorrect="false"
        required
        {...register(name)}
        {...props}
        ref={ref}
      />
      <LabelContainer className="label">
        <Label className="content">{label}</Label>
      </LabelContainer>
    </Form>
  );
});

const Form = styled.div`
  position: relative;
  width: 100%;
  height: 6rem;
  font-size: 1.6rem;
  overflow: hidden;
  margin-top: 1rem;
`;

const Input = styled.input`
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  padding-top: 2.4rem;
  font-size: 1.8rem;

  &:focus + .label .content {
    font-size: 1.4rem;
    transform: translateY(-200%);
    color: ${colors.SECONDARY};
  }
  &:focus + .label::after {
    transform: translateX(0);
  }
  &:valid + .label .content {
    font-size: 1.4rem;
    transform: translateY(-200%);
    color: ${colors.SECONDARY};
  }
`;

const LabelContainer = styled.label`
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid ${colors.GRAY100};

  ::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0px;
    left: 0px;
    border-bottom: 3px solid ${colors.SECONDARY};
    transform: translateX(-100%);
    transition: transform 0.3s ease;
  }
`;

const Label = styled.span`
    position: absolute;
    bottom: 5px;
    left: 0;
    transition: all 0.3s ease;
`;

export default TextInput;