import React, { useState, useRef, useEffect } from 'react';

import styled from 'styled-components';


const CountDown = ({ date, Oepning }) => {
  const [ timerDays, setTimerDays ] = useState('00')
  const [ timerHours, setTimerHours ] = useState('00')
  const [ timerMinutes, setTimerMinutes ] = useState('00')
  const [ timerSeconds, setTimerSeconds ] = useState('00')
  const [ stop, setStop ] = useState(false)

  let interval = useRef()

  const StartTimer = () => {
    const countdownDate = new Date(date).getTime()

    interval = setInterval(() => {
      const now = new Date().getTime()
      const distance = countdownDate - now

      const days = Math.floor(distance / (1000 * 60 * 60 * 24))
      const hours = Math.floor(distance % (1000 * 60 * 60 * 24) / (1000 * 60 * 60))
      const minutes = Math.floor(distance % (1000 * 60 * 60) / (1000 * 60))
      const seconds = Math.floor(distance % (1000 * 60) / 1000)

      if (distance < 0) {
        clearInterval(interval)
        setStop(true)
        Oepning(true)
      } else {
        setTimerDays(days)
        setTimerHours(hours)
        setTimerMinutes(minutes)
        setTimerSeconds(seconds)
      }

    }, 1000);
  }

  useEffect(() => {
    if (!stop) {
      StartTimer()
    }
    return () => {
      Oepning(true)
      setStop(true)
      clearInterval(interval)
    }
  }, [])

  return (
    <CountDownContainer>
      <div className="dayContainer">
        <h3 className="number">{timerDays}</h3>
        <h3 className="tag">일</h3>
      </div>
      <div className="dayContainer">
        <h3 className="number">{timerHours}</h3>
        <h3 className="tag">시</h3>
      </div>
      <div className="dayContainer">
        <h3 className="number">{timerMinutes}</h3>
        <h3 className="tag">분</h3>
      </div>
      <div className="dayContainer">
        <h3 className="number">{timerSeconds}</h3>
        <h3 className="tag">초</h3>
      </div>
    </CountDownContainer>
  );
}

const CountDownContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2rem 2rem 2rem 0;
  border: 2px solid #fff;
  border-radius: 10px;
  width: 80%;
  height: 80px;

  .dayContainer {
    display: flex;
    flex-direction: column;
    padding-left: 2rem;

    .number {
      font-size: 3.2rem;
      color: #fff;
    }
    .tag {
        font-size: 1.4rem;
        color: #fff;
      }
  }

  @media screen and (min-width: 768px) {
    padding: 2rem 3rem 2rem 0;

    .dayContainer {
      display: flex;
      flex-direction: column;
      padding-left: 3rem;

      .number {
        font-size: 3.2rem;
      }
      .tag {
        font-size: 1.4rem;
      }
    }
  }
`;

export default CountDown;