import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import { useForm } from "react-hook-form";
import Button from '../../../components/Button';
import InnerContainer from '../../../components/Container/InnerContainer';
import { colors } from '../../../styles/colors';


import { db } from '../../../config/firebaseConfig';
import { SettingContext } from '../../../context/SettingContext';

const SelfConfirmSection = ({ setIsChecking, setResult }) => {
  const { register, handleSubmit, formState: { errors, isSubmitting }} = useForm()
  const [ setting ] = useContext(SettingContext)

  const onSubmit = (data) => {
    console.log(data)

    const userListRef = db.collection('주일예배').doc(data.session).collection(setting.sunday)
    let indexQuery = userListRef.where("index", "==", 80)
    let query = userListRef.where("name", "==", data.name).where("position", "==", data.position);
    query.get().then((querySnapshot) => {
      if (querySnapshot.empty) {
        console.log('No register data')
        setResult({
          index: 0,
          name: data.name,
          position: data.position,
          session: data.session,
          reservation: false,
        })
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index < 80) {
            console.log('선착순 당첨')
            setResult({
              index: doc.data().index,
              name: data.name,
              position: data.position,
              session: data.session,
              reservation: true,
            })
          } else if (doc.data().index === 80) {
            indexQuery.get().then((querySnapshot) => {
              let tempArray = []
              querySnapshot.forEach((doc, idx) => {
                tempArray.push({
                 id: doc.id,
                 order: idx,
                 ...doc.data()
                })
             })
             tempArray.sort((a, b) => {
               return a.submitTime - b.submitTime
             })
             if (tempArray[0].name === data.name) {
                console.log('선착순 당첨')
                setResult({
                  index: doc.data().index,
                  name: data.name,
                  position: data.position,
                  session: data.session,
                  reservation: true,
                })
              } else {
                console.log('제출시간에서 밀림')
                setResult({
                  index: doc.data().index,
                  name: data.name,
                  position: data.position,
                  session: data.session,
                  reservation: false,
                })
              }
            })
          } else {
            console.log('80명이후 등록')
            setResult({
              index: doc.data().index,
              name: data.name,
              position: data.position,
              session: data.session,
              reservation: false,
            })
          }
        })
      }
    })
    setIsChecking(true)
  }

  return (
    <InnerContainer>
      <ConfirmContainer>
        <InfoContainer>
          <p className="infoTitle">신청해주셔서 감사합니다</p>
          <p>신청하신 결과는 아래에서 확인해주세요</p>
        </InfoContainer>
        <FormContainer onSubmit={handleSubmit(onSubmit)}>
            <InputContainer>
              <label htmlFor="session" >신청예배</label>
              <select 
                defaultValue="1부"
                {...register("session",  { required: true })}
              >
                <option value="1부예배">1부예배</option>
                <option value="2부예배">2부예배</option>
                <option value="3부예배">3부예배</option>
                <option value="4부예배">4부예배</option>
                <option value="6부예배">6부예배</option>
              </select>
            </InputContainer>
            <InputContainer>
              <label htmlFor="name">이름</label>
              <input 
                type="text" 
                name="name" 
                autoComplete="off" 
                placeholder="본인이름만 입력해 주세요."
                {...register('name', { required: true, maxLength: 5 })}
              />
              {errors.name && <ErrorMsgBox>이름을 입력해주세요</ErrorMsgBox>}
            </InputContainer>
            <InputContainer>
              <label htmlFor="position">직분</label>
              <input 
                type="text" 
                name="position" 
                autoComplete="off" 
                placeholder="장로/권사/집사/성도/청년/학생"
                {...register('position', { required: true })}
              />
              {errors.position && <ErrorMsgBox>직분을 입력해주세요</ErrorMsgBox>}
            </InputContainer>
            <ButtonContainer>
              <Button
                disabled={ isSubmitting }
              >
                {isSubmitting ? '확인 중': '확인하기'}
              </Button>
            </ButtonContainer>
          </FormContainer>
      </ConfirmContainer>
    </InnerContainer>
  );
}

const ConfirmContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
`;

const FormContainer = styled.form`
  display: block;
  width: 100%;
  margin: 2rem 0 2.4rem;

  @media screen and (min-width: 768px) {
    max-width: 500px;
  }
`;

const InfoContainer = styled.div`
  width: 100%;
  padding: 2rem;
  color: #fff;
  font-size: 1.6rem;
  font-weight: lighter;
  background-color: ${colors.SECONDARY};
  line-height: 1.6;

  .infoTitle {
    font-size: 2.0rem;
    font-weight: bold;
    letter-spacing: 0.2rem;
    padding-bottom: 3rem;
  }

  @media screen and (min-width: 768px) {
    max-width: 500px;
  }
`;

const InputContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 2.4rem;

  label {
    font-size: 1.4rem;
  }
  input {
    font-size: 1.8rem;
    outline: none;
    border: none;
    border-bottom: 2px solid ${colors.SECONDARY};
    margin-bottom: 0.5rem;
    padding-top: 1.6rem;
    padding-left: 0.3rem;

    ::placeholder {
      font-size: 1.4rem;
    }
  }
  select {
    margin-top: 1rem;
    padding: 1rem 0.3rem;
    border: 2px solid ${colors.SECONDARY};
    font-size: 1.8rem;
  }
`;

const ErrorMsgBox = styled.p`
  margin-top: 0.5rem;
  color: ${colors.WARNING};
  font-size: 1.4rem;
`;



const ButtonContainer = styled.div`
  width: 100%;
  margin: 4rem 0;
`;

export default SelfConfirmSection;