import React, { useContext } from 'react';
import styled from 'styled-components';

import { UserContext } from '../../../context/UserContext';
import { SettingContext } from '../../../context/SettingContext';
import { db } from '../../../config/firebaseConfig';

import InnerContainer from '../../../components/Container/InnerContainer';
import ConfirmImage from '../../../assets/confirmedImage02.svg'
import { colors } from '../../../styles/colors';
import Button from '../../../components/Button';


const ResultLanding = ({setIsChecked, setCheckResult}) => {
  const [ user ] = useContext(UserContext)
  const [ setting ] = useContext(SettingContext)
  


  const onHandleClick = () => {
    const userListRef = db.collection('주일예배').doc(user.session).collection(setting.sunday)
    let indexQuery = userListRef.where("index", "==", 80)
    let query = userListRef.where("name", "==", user.name).where("position", "==", user.position);
    query.get().then((querySnapshot) => {
      if (querySnapshot.empty) {
        console.log('No register data')
        setCheckResult({
          index: 999,
          name: user.name,
          position: user.position,
          session: user.session,
          reservation: false,
        })
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index < 80) {
            console.log('선착순 당첨')
            setCheckResult({
              index: doc.data().index,
              name: user.name,
              position: user.position,
              session: user.session,
              reservation: true,
            })
          } else if (doc.data().index === 80) {
            indexQuery.get().then((querySnapshot) => {
              let tempArray = []
              querySnapshot.forEach((doc, idx) => {
                tempArray.push({
                 id: doc.id,
                 order: idx,
                 ...doc.data()
                })
             })
             tempArray.sort((a, b) => {
               return a.submitTime - b.submitTime
             })
             if (tempArray[0].name === user.name) {
                console.log('선착순 당첨')
                setCheckResult({
                  index: doc.data().index,
                  name: user.name,
                  position: user.position,
                  session: user.session,
                  reservation: true,
                })
              } else {
                console.log('제출시간에서 밀림')
                setCheckResult({
                  index: doc.data().index,
                  name: user.name,
                  position: user.position,
                  session: user.session,
                  reservation: false,
                })
              }
            })
          } else {
            console.log('80명이후 등록')
            setCheckResult({
              index: doc.data().index,
              name: user.name,
              position: user.position,
              session: user.session,
              reservation: false,
            })
          }
        })
      }
    })
    setIsChecked(true)
  }

  return (
    <InnerContainer>
      <LandingContainer>
        <HeaderContainer>
          <img src={ConfirmImage} alt="icon close" />
          <p>예배신청 되셨습니다</p>
          <p>결과 확인은 아래 버튼을 눌러주세요</p>
        </HeaderContainer>
        <ButtonContainer>
          <Button onClick={() => onHandleClick()}>결과확인</Button>
        </ButtonContainer>
      </LandingContainer>
    </InnerContainer>
  );
}

const LandingContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 94vh;
  padding: 0 3rem;
`;

const HeaderContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  color: ${colors.BLACK};
  line-height: 1.3;
  font-weight: 600;
  margin-bottom: 5rem;

  img {
    width: 150px;
    height: auto;
    object-fit: contain;
    margin-bottom: 2rem;
  }
`;

const ButtonContainer = styled.div`
  width: 100%;
`;

export default ResultLanding;