import React, { useState, useContext } from 'react';
import { Route, Switch, BrowserRouter, Redirect } from 'react-router-dom'
import dotenv from 'dotenv';

import { SettingProvider } from './context/SettingContext'
import { UserContext, UserProvider } from './context/UserContext';

import GlobalStyle from './styles/GlobalStyle';
import NavBar from './components/NavBar';
import SideBar from './components/SideBar';
import HomePage from './pages/HomePage/HomePage';
import BookingPage from './pages/BookingPage/BookingPage';
import AdminPage from './pages/AdminPage/AdminPage';
import LoadingPage from './pages/LoadingPage/LoadingPage';
import CheckPage from './pages/CheckPage/CheckPage';
import ResultPage from './pages/ResultPage/ResultPage';

dotenv.config();

function App() {
  const [ loading, setLoading ] = useState(true)
  const [ isOpen, setIsOpen ] = useState(false)

  const onToggle = () => {
    setIsOpen(!isOpen)
  }
  
  return (
    <SettingProvider>
      <UserProvider>
        <BrowserRouter basename={"/"}>
          <GlobalStyle />
          <NavBar onToggle={onToggle} />
          <SideBar isOpen={isOpen} onToggle={onToggle} />
          <Switch>
            <Route exact path="/service-reservation/">
              {loading ? <LoadingPage loading={loading} setLoading={setLoading}/> : <Redirect to="/service-reservation/main" />}
            </Route>
            <Route exact path="/service-reservation/main" component={HomePage} />
            <Route exact path="/service-reservation/booking" component={BookingPage} />
            <Route exact path="/service-reservation/result" component={ResultPage} />
            <Route exact path="/service-reservation/check" component={CheckPage} />
            <Route exact path="/service-reservation/admin" component={AdminPage} />
          </Switch>
        </BrowserRouter>
      </UserProvider>
    </SettingProvider>
  );
}

export default App;
