import React, { useState } from 'react';
import styled from 'styled-components';
import InnerContainer from '../../../components/Container/InnerContainer';
import { colors } from '../../../styles/colors';
import { FaCog, FaSearch } from "react-icons/fa";
import Setting from './Setting';
import Search from './Search';

const Dashboard = () => {
  const [ isLeft, setIsLeft ] = useState(true)

  return (
    <InnerContainer>
      <DashboardContainer>
        <Menu>
          <MenuTab onClick={() => setIsLeft(true)}>
            <FaCog />
            <p>설정</p>
          </MenuTab>
          <MenuTab onClick={() => setIsLeft(false)}>
            <FaSearch />
            <p>조회</p>
          </MenuTab>
        </Menu>
        <Main>
          {isLeft ? <Setting /> : <Search />}
        </Main>
      </DashboardContainer>
    </InnerContainer>
  );
}

const DashboardContainer = styled.div`
  width: 100%;
`;
const Menu = styled.ul`
  display: flex;
  background-color: ${colors.GRAY800};
`;

const MenuTab = styled.li`
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.6rem 0;
  font-size: 1.6rem;
  color: ${colors.WHITE};
  cursor: pointer;

  &:first-child {
    border-right: 1px solid ${colors.GRAY100};
  }

  p {
    margin-left: 1rem;
  }

  @media screen and (min-width: 768px) {
    padding: 2.4rem 0;
  }
`;


const Main = styled.div``;

export default Dashboard;