import React, { useState, useContext } from 'react';
import { useEffect } from 'react';
import { useForm } from "react-hook-form";
import styled from 'styled-components';
import Button from '../../components/Button';
import InnerContainer from '../../components/Container/InnerContainer';
import OuterContainer from '../../components/Container/OuterContainer';
import Modal from '../../components/Modal';

import { SettingContext } from '../../context/SettingContext';
import { UserContext } from '../../context/UserContext';
import { colors } from '../../styles/colors';

const BookingPage = ({ location }) => {
  const { register, handleSubmit, formState: { errors, isSubmitting }} = useForm()
  const [ isOpen, setIsOpen ] = useState(false);
  const [ setting ] = useContext(SettingContext)
  const [ user, setUser ] = useContext(UserContext)
  const [ subtitle, setSubtitle ] = useState("")


  const { title } = setting
  const serviceTime = location.search.split("=")[1]

  console.log(subtitle)
  
  const getSubtitle = (serviceTime) => {
    switch(true) {
      case serviceTime ===  'sessionOne': 
        return "1부예배"
      case serviceTime === 'sessionTwo': 
        return "2부예배"
      case serviceTime === 'sessionThree': 
        return "3부예배"
      case serviceTime === 'sessionFour': 
        return "4부예배"
      case serviceTime === 'sessionFive': 
        return "5부예배"
      case serviceTime === 'sessionSix': 
        return "6부예배"
      default:
        return '1부예배'
    }
  }

  useEffect(() => {
    setSubtitle(getSubtitle(serviceTime))
  }, [])


  const onSubmit = (data) => {
    console.log(data)
    setUser({
      ...user,
      name: data.name,
      position: data.position,
      division: data.division,
      session: subtitle,
      isSubmitted: true
    })
    setIsOpen(true);
  }

  const closeModal = () => {
    setUser({
      ...user,
      name: "",
      position: "",
      division: "",
      session: "",
      isSubmitted: false
    })
    setIsOpen(false)
  }

  return (
    <OuterContainer>
      <InnerContainer>
        <ReservationContainer>
          <InfoContainer>
            <p className="infoTitle">{title}</p>
            <p>{subtitle}</p>
          </InfoContainer>
          <FormTitle>예배 신청서</FormTitle>
          <FormContainer onSubmit={handleSubmit(onSubmit)}>
            <InputContainer>
              <label htmlFor="name">이름</label>
              <input 
                type="text" 
                name="name" 
                autoComplete="off" 
                placeholder="본인이름만 입력해 주세요."
                {...register('name', { required: true, maxLength: 5 })}
              />
              {errors.name && <ErrorMsgBox>이름을 입력해주세요</ErrorMsgBox>}
            </InputContainer>
            <InputContainer>
              <label htmlFor="position">직분</label>
              <input 
                type="text" 
                name="position" 
                autoComplete="off" 
                placeholder="장로/권사/집사/성도/청년/학생"
                {...register('position', { required: true })}
              />
              {errors.position && <ErrorMsgBox>직분을 입력해주세요</ErrorMsgBox>}
            </InputContainer>
            <InputContainer>
              <label htmlFor="division" >소속</label>
              <input 
                type="text" 
                name="division" 
                autoComplete="off" 
                placeholder="남선교회/여선교회/시니어/청장년/영커플"
                {...register('division', { required: true })}
              />
              {errors.division && <ErrorMsgBox>소속을 입력해주세요</ErrorMsgBox>}
            </InputContainer>
            <ButtonContainer>
              <Button
                disabled={ isSubmitting }
              >
                {isSubmitting ? '제출중': '제출하기'}
              </Button>
            </ButtonContainer>
          </FormContainer>
          <Modal isOpen={isOpen} closeModal={closeModal} />  
        </ReservationContainer>
      </InnerContainer>
    </OuterContainer>
  );
}

const ReservationContainer = styled.div`
  padding: 1rem 3rem;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;


const InfoContainer = styled.div`
  width: 100%;
  padding: 2rem;
  color: #fff;
  font-size: 1.8rem;
  font-weight: lighter;
  background-color: ${colors.SECONDARY};
  line-height: 1.6;

  .infoTitle {
    font-size: 2.4rem;
    font-weight: bold;
    letter-spacing: 0.2rem;
    padding-bottom: 3rem;
  }

  @media screen and (min-width: 768px) {
    max-width: 500px;
  }
`;

const FormTitle = styled.p`
  color: #1e1e23;
  letter-spacing: -0.3px;
  font-size: 2.2rem;
  margin: 1.6rem 0 2.4rem;
`;

const FormContainer = styled.form`
  display: block;
  width: 100%;

  @media screen and (min-width: 768px) {
    max-width: 500px;
  }
`;

const InputContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 2.4rem;

  label {
    font-size: 1.4rem;
  }
  input {
    font-size: 1.8rem;
    outline: none;
    border: none;
    border-bottom: 2px solid ${colors.SECONDARY};
    margin-bottom: 0.5rem;
    padding-top: 1.6rem;
    padding-left: 0.3rem;

    ::placeholder {
      font-size: 1.4rem;
    }
  }
  select {
    margin-top: 1rem;
    padding: 1rem 0.3rem;
    border: 2px solid ${colors.SECONDARY};
    font-size: 1.8rem;
  }
`;

const ErrorMsgBox = styled.p`
  margin-top: 0.5rem;
  color: ${colors.WARNING};
  font-size: 1.4rem;
`;



const ButtonContainer = styled.div`
  width: 100%;
  margin: 4rem 0;
`;

export default BookingPage;