import React, { useContext, useEffect, useState } from 'react';
import { css } from "@emotion/react";
import styled from '@emotion/styled'
import HashLoader from "react-spinners/HashLoader";
import { colors } from '../../styles/colors';
import { SettingContext } from '../../context/SettingContext';
import { db } from '../../config/firebaseConfig';
import { fillZero } from '../../utils/utils';

import thumbnail01 from '../../assets/thumbnail01.png'
import thumbnail02 from '../../assets/thumbnail02.jpeg'
import thumbnail03 from '../../assets/thumbnail03.png'
import thumbnail04 from '../../assets/thumbnail04.jpeg'
import thumbnail05 from '../../assets/thumbnail05.png'
import thumbnail06 from '../../assets/thumbnail06.png'




const LoadingPage = ({ loading, setLoading }) => {
  const [ setting, setSetting ] = useContext(SettingContext)

  const initialize = async () => {
    const settingRef = db.collection('setting').doc('latest')
    await settingRef.get()
    .then((doc) => {
      if (doc.exists) {
        setSetting({
          ...setting,
          sunday: doc.data().sunday,
          openingDate: doc.data().openingDate,
          title:doc.data().title,
          subtitle: doc.data().subtitle,
          session: [
            {
              serviceId: 'sessionOne',
              serviceName: "1부예배",
              time: "오전 7:10",
              notice: doc.data().sessionOneNotice,
              seats: doc.data().sessionOneSeats,
              forceClosing: doc.data().sessionOneClosing,
              thumbnail: thumbnail01,
              bgColor: colors.LIGHTBLUE200,
            },
            {
              serviceId: 'sessionTwo',
              serviceName: "2부예배",
              time: "오전 8:00",
              notice: doc.data().sessionTwoNotice,
              seats: doc.data().sessionTwoSeats,
              forceClosing: doc.data().sessionTwoClosing,
              thumbnail: thumbnail02,
              bgColor: colors.LIGHTBLUE300,
            },
            {
              serviceId: 'sessionThree',
              serviceName: "3부예배",
              time: "오전 9:30",
              notice: doc.data().sessionThreeNotice,
              seats: doc.data().sessionThreeSeats,
              forceClosing: doc.data().sessionThreeClosing,
              thumbnail: thumbnail03,
              bgColor: colors.LIGHTBLUE400,
            },
            {
              serviceId: 'sessionFour',
              serviceName: "4부예배",
              time: "오전 11:00",
              notice: doc.data().sessionFourNotice,
              seats: doc.data().sessionFourSeats,
              forceClosing: doc.data().sessionFourClosing,
              thumbnail: thumbnail04,
              bgColor: colors.LIGHTBLUE500,
            },
            {
              serviceId: 'sessionFive',
              serviceName: "5부예배",
              time: "오후 2:00",
              notice: "인터치 청년예배입니다",
              seats: 0,
              forceClosing: true,
              thumbnail: thumbnail05,
              bgColor: colors.LIGHTBLUE600,
            },
            {
              serviceId: 'sessionSix',
              serviceName: "6부예배",
              time: "오후 4:00",
              notice: doc.data().sessionSixNotice,
              seats: doc.data().sessionSixSeats,
              forceClosing: doc.data().sessionSixClosing,
              thumbnail: thumbnail06,
              bgColor: colors.LIGHTBLUE700,
            },
          ]
        })
      } else {
        console.log("세팅데이터가 없습니다")
      }
    }).catch((error) => {
      console.error("Error initialize: ", error);
    })
  }

  useEffect(() => {
    initialize()
    .then(() => {
      setLoading(false)
    })
  }, [])

  return (
    <Container>
      <TextBox>
        <span className="welcome">
          안녕하세요 <br />
        </span>
        <span className="intro">화양교회 예배신청 페이지입니다</span>
      </TextBox>
      <HashLoader loading={loading} css={spinnerStyle} size={50} color={colors.PRIMARY}/>
    </Container>
  );
}

const Container = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const TextBox = styled.div`
  margin-bottom: 1.6rem;

  .welcome {
    font-family: 'jua', sans-serif;
    font-size: 2rem;
    font-weight: 700;
    color: ${colors.SECONDARY}
  }

  .intro {
    font-family: 'jua', sans-serif;
    font-size: 1.4rem;
    font-weight: 400;
  }
`;



const spinnerStyle = css`
  display: block;
  margin: 0 auto;
`;

export default LoadingPage;