import React from 'react';
import styled from 'styled-components';
import { colors } from '../styles/colors';
import { NavLink } from 'react-router-dom';
import { AiOutlineClose } from 'react-icons/ai'


const SideBar = ({ isOpen, onToggle }) => {
  return (
    <Container isOpen={isOpen}>
      <SidebarToggle onClick={onToggle}>
        <AiOutlineClose />
      </SidebarToggle>
      <SidebarMenu isOpen={isOpen}>
        <li><NavLink to="/service-reservation" onClick={onToggle}>예배신청</NavLink></li>
        <li><NavLink to="/service-reservation/check" onClick={onToggle}>신청결과확인</NavLink></li>
        <li><NavLink to="/service-reservation/admin" onClick={onToggle}>관리자페이지</NavLink></li>
        </SidebarMenu>
    </Container>
  );
}

const Container = styled.aside`
  position: fixed;
  z-index: 2001;
  width: 100%;
  height: 100%;
  background: ${colors.PRIMARY};
  display: grid;
  align-items: flex-start;
  justify-content: center;
  top: 0;
  left: 0;
  padding: 15rem 0;
  transition: 0.3s ease-in-out;
  opacity: ${({ isOpen }) => (isOpen ? '100%' : '0')};
  top: ${({ isOpen }) => (isOpen ? '0' : '-100%')};
`;

const SidebarToggle = styled.div`
  color: #000;
  position: absolute;
  top: 2rem;
  right: 4rem;
  min-height: 2rem;
  min-width: 2rem;
  font-size: 1.8rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  cursor: pointer;
  outline: none;
`;

const SidebarMenu = styled.ul`
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 80px);
  text-align: center;
  
  li {
    font-size: 2rem;
    font-weight: bolder;

    a {
      color: #fff;
      text-decoration: none;
    }
  }
`;

export default SideBar;